*{
    margin: 0;
    box-sizing: border-box;
}

.page {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cover{
    background-color: white;
    width: 30em;
    height: 35em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 1em;
    box-shadow: 0 0.188em 1.550em rgb(156, 156, 156);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;    
}

.alt-login div {
    width: 45%;
    height: 90%;
    border-radius: 0.25em;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
}

input{
    border: 1px solid #ccc;
    border-radius: 100px;
    background-color: rgb(255, 255, 255);
    height: 0em;
    width: 80%;
    border-radius: 0,25em;
    text-align: center;
    padding: 2em;
}

input:focus{
    outline-color:#2c772c
}

.login-btn{
    width:40%;
    height: 3em;
    background-color:#2c772c;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    border-radius: 0.25em;
    cursor: pointer;
}

.login-btn:hover{transform: scale(1.25);}

.text{
    position:relative;
    bottom: -2em;
    color: #000;
}

.hide{
    position: absolute;
    transform: scale(0);
}

